@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');

$min-width: 70%;
$max-width: 90%;

form{
    width: 100%;
    position: relative;
    height: max-content;
    overflow: hidden;
    transition: .3s ease;
    // border: 1px solid red;
    
}
.form-collapse{
    height: 0;
}

.checkbox-general{
    width: 25px;
    height: 25px;
    vertical-align:bottom;
    position: relative;
    top: -3px;

    &:hover{
        cursor: pointer;
    }
}
.addOptions-grid{
    min-width: 70%;
    max-width: 90%;
    margin: 10px auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr); 
    text-align: center;
    font-size: 1.5rem;
    font-family: 'Work Sans', sans-serif;
    font-weight: bold;

    label{
        width: max-content;

        &:nth-child(1) > input[type="checkbox"]:hover{
            cursor: default;
        }

        &:nth-child(2){
            justify-self: center;
            @media screen and (max-width:1024px) {
                justify-self: start;
            }
        }

        &:nth-child(3){
            justify-self: end;
            @media screen and (max-width:1024px) {
                justify-self: start;
            }
        }

        
    }
    input[type="checkbox"]{
        text-align:start;
        margin-right: 3px;
    }
    
    @media screen and (max-width: 1024px){
        grid-template-columns: 1fr;
        text-align: left;
    }
}

input[type=text]{
    font-size: 2rem;
    border: 1px solid rgb(174, 168, 168);
    padding: 5px 15px;
    min-width: 70%;
    width: 90%;
    margin: 10px auto;
    display: block;
    outline: none;
    border-radius: 15px;

    &:focus{
        background-color: rgb(243, 246, 247);
        -webkit-box-shadow: 0px 5px 13px 3px rgba(66, 68, 90, 0.46);
        -moz-box-shadow: 0px 5px 13px 3px rgba(66, 68, 90, 0.46);
        box-shadow: 0px 5px 13px 3px rgba(66, 68, 90, 0.46);
    }
}

textarea{
    min-width: 70%;
    width: 90%;
    border: 1px solid rgb(174, 168, 168);
    padding: 5px 15px;
    resize: none;
    margin: 10px auto;
    display: block;
    font-size: 2rem;
    transition: .3s ease;
    outline: none;
    border-radius: 15px;
    
    

    &:focus{
        background-color: rgb(243, 246, 247);
    }
}

.priority-grid{
    width: 90%;
    min-width: 70%;
    margin: 10px auto;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: start;
    font-size: 1.5rem;
    transition: .3s ease;

    input[type="checkbox"]{
        margin-right: 10px;
    }
}

.hide{
    transition: height .4s ease .1s, padding .4s ease .3s, border .5s ease .1s;
    height: 0px;
    overflow: hidden;
    padding: 0;
    border: 1px solid #fff;
}
.collapse{
    transition: height .4s ease .1s, padding .4s ease .1s, border .4s ease .1s, overflow .01s ease .01s;
    height: 350px; 
}
.hide-priorityGrid{
    height: 0px;
    overflow: hidden;
}
.collapse-priorityGrid{
    height: 130px;
    position: relative;
    animation: bounce ease-in-out .4s .2s;
}
@keyframes bounce{
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-15px);
    }
    100%{
        transform: translateY(0);
    }
}

.addTodo{
    font-size: 2rem;
    font-family: 'Work Sans', sans-serif;
    padding: 5px 10px;
    font-weight: bold;
    float: right;
    margin-right: 5%;
    background-color: #ace6ae;
    border-radius: 5px;
    transition: .2s ease-out;
    color: rgb(92, 88, 88);

    &:hover{
        cursor: pointer;
        transform: translateY(-10px);
        color: black;
    }

    .add-icon{
        background: url(../../icons/icon1.png);
        background-size: cover;
        float: left;
        width: 40px;
        height: 40px;
    }
}
